.content-header {
  text-align: center;
  margin-bottom: 50px;
  color: $primary-color;

  h1 {
    font-size: calc(18vw - 20px);
    line-height: 0.8em;
    margin: 25px 0;

    @media screen and (min-width: 800px) {
      font-size: 124px;
    }

    span {
      font-size: 80%;
    }
  }

  p {
    margin: 0;
    font-size: 24px;
    font-weight: bold;
  }
}
