@import '../utils/functions';

// -- Colors
$black: #000;
$white: #fff;
$dark-gray: #222;
$red: #f00;

$primary-color: $red;
$base-color: $black;

// -- Typography
$font-family-serif: serif;
$font-family-sans-serif: "Roboto", sans-serif;
$font-family-monospace: monospace;
$font-family-base: $font-family-sans-serif;
$font-family-headings: "Galada", sans-serif;

$font-size-base: 16px;
$font-size-h1: ( 48 / strip-unit($font-size-base) ) * 1rem;
$font-size-h2: ( 34 / strip-unit($font-size-base) ) * 1rem;
$font-size-h3: ( 24 / strip-unit($font-size-base) ) * 1rem;
$font-size-h4: ( 20 / strip-unit($font-size-base) ) * 1rem;
$font-size-h5: 1rem;
$font-size-h6: 0.8rem;

// Unit-less `line-height` for use in components like buttons.
$line-height-base: 20/16;
// Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
$line-height-computed: floor(($font-size-base * $line-height-base));
