.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #9999cc;
  overflow: hidden;

  .wrap {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .sun, .sun-shadow {
    position: absolute;
    top: -13vw;
    right: -13vw;
    width: 26vw;
    height: 26vw;
    border-radius: 13vw;
  }

  .sun {
    background-color: #ffff00;
    animation: 5s infinite linear sun;
  }

  .sun-shadow {
    background-color: #646400;
    animation: 5s infinite linear sun;
    animation-fill-mode: both;
    animation-delay: -2s;
  }

  .grass {
    position: absolute;
    left: 0;
    top: 40%;
    width: 100%;
    height: 60%;
    background: linear-gradient(0deg, #166401 50%, #779831 100%);
  }

  .sea {
    position: absolute;
    left: 0;
    top: 60%;
    width: 100%;
    height: 40%;
    background: repeating-linear-gradient(0deg, #5050ff, #5050ff 10%, #9698ff 30%, #5050ff 35%);
    background-position-x: 0%;
    background-size: 200px 200px;
    animation: 14s infinite linear sea;

  }

  .vikingline-cont {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 82%;
    animation: 5s infinite ease-in-out shipWave;
    animation-fill-mode: both;
    animation-delay: -1s;
    overflow: hidden;
  }

  .vikingline-pos {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.vikingline {
  position: absolute;
  left: 15%;
  bottom: -5%;
  width: 600px;
  height: 200px;
  transform-origin: bottom left;
  transform: rotate(-30deg);

  .bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 30%;
    width: 80%;
    background-color: #f00;
  }

  .bottom:after {
    content: " ";
    position: absolute;
    right: -20%;
    height: 100%;
    width: 80%;
    transform: skewX(-50deg);
    background-color: #f00;
  }

  .text {
    position: absolute;
    bottom: -5%;
    left: 20%;
    height: 30%;
    width: 80%;
    text-transform: uppercase;
    font-size: 40px;
    font-family: sans-serif;
    font-weight: bold;
    line-height: 1em;
    color: #fff;
    z-index: 3;
  }

  .mid {
    position: absolute;
    bottom: 30%;
    left: 15%;
    width: 70%;
    height: 32%;
    background-color: #fff;
  }

  .mid:before {
    content: " ";
    position: absolute;
    left: -10%;
    height: 100%;
    width: 20%;
    transform: skewX(-10deg);
    background-color: #fff;
  }

  .mid:after {
    content: " ";
    position: absolute;
    right: -10%;
    height: 100%;
    width: 20%;
    transform: skewX(30deg);
    background-color: #fff;
  }

  .top {
    position: absolute;
    bottom: 60%;
    left: 25%;
    width: 56.6%;
    height: 22%;
    background-color: #fff;
  }

  .top:before {
    content: " ";
    position: absolute;
    left: -10%;
    height: 100%;
    width: 20%;
    transform: skewX(-15deg);
    background-color: #fff;
  }

  .top:after {
    content: " ";
    position: absolute;
    right: -10%;
    height: 100%;
    width: 20%;
    transform: skewX(30deg);
    background-color: #fff;
  }

  .pipe-bottom {
    position: absolute;
    bottom: 80%;
    left: 30%;
    width: 10%;
    height: 10%;
    background-color: #fff;
  }

  .pipe-bottom:after {
    content: " ";
    position: absolute;
    right: -20%;
    height: 100%;
    width: 50%;
    transform: skewX(50deg);
    background-color: #fff;
  }

  .pipe-top {
    position: absolute;
    bottom: 90%;
    left: 30%;
    width: 5%;
    height: 15%;
    background-color: #f00;
  }

  .pipe-top:after {
    content: " ";
    position: absolute;
    right: -50%;
    height: 100%;
    width: 100%;
    transform: skewX(45deg);
    background-color: #f00;
  }
}

@keyframes sun {
  0% {
    transform: translate(0, 5%);
  }
  10% {
    transform: translate(2.9%, 4%)
  }
  20% {
    transform: translate(4.7%, 1.5%)
  }
  30% {
    transform: translate(4.7%, -1.5%)
  }
  40% {
    transform: translate(2.9%, -4%)
  }
  50% {
    transform: translate(0%, -5%)
  }
  60% {
    transform: translate(-2.9%, -4%)
  }
  70% {
    transform: translate(-4.7%, -1.5%)
  }
  80% {
    transform: translate(-4.7%, 1.5%)
  }
  90% {
    transform: translate(-2.9%, 4%)
  }
  100% {
    transform: translate(0, 5%);
  }
}

@keyframes shipTilt {
  from {
    transform: rotate(-30deg);
  }
  to {
    transform: rotate(5deg);
  }
}

@keyframes shipWave {
  0% {
    transform: translate(0, 1%);
  }
  50% {
    transform: translate(0, -1%)
  }
  100% {
    transform: translate(0, 1%);
  }
}

@keyframes shipSink {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10%);
  }
}

@keyframes sea {
  0% {
    background-position-y: 0;
  }
  100% {
    background-position-y: 200px;
  }
}

.content {
  position: relative;
  z-index: 2;
}
